.hamburger-menu {
    vertical-align: middle;
    position: relative;
    height: 20px;
    width: 24px;
    display: flex;
    align-items: center;

    &:hover {
        transform: rotate(360deg) scale(0);
        transition: 0.5s;
    }
}

.hamburger-menu__bars {
    &, &:before, &:after {
        background: #fff;
        width: 24px;
        height: 3px;
        border-radius: 3px;
    }

    &:before, &:after {
        content: "";
        display: block;
        position: absolute;
    }

    &:before {
        top: 0;
    }

    &:after {
        bottom: 0;
    }
}