.maincontent {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.section {
    background-color: $gray;
    flex-grow: 1;

}

.container {
    height: 100%;
    max-height: 1225px;
    width: 1290px;
    margin:25px auto;

}

.button {
    display: inline-block;
    background-color: $dark;
    color: #fff;
    font-family: 'Roboto', sans-serif;
    font-size: 12px;
    font-weight: bold;
    text-transform: uppercase;
    border-radius: 3px;
    cursor: pointer;
}

