.payment__list {
  display: flex;
  align-items: center;
}

.payment__icon {
  display: inline-block;
  height: 20px;
  width: 20px;
  overflow: hidden;
  position: relative;
  margin-right: 5px;

  &--visa {
    height: 30px;
    width: 40px;
  }

  &--mc {
    height: 30px;
    width: 40px;
  }

  &--sber {
    height: 80px;
    width: 80px;
    position: absolute;
    left: -12px;
  }
}