.socials {
  margin-bottom: 40px;
}

.socials__title {
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  font-size: 15px;
  color: $dark;
  margin-bottom: 25px;
}

.socials__buttons {
  // display: flex;
}

.socials__button {
  display: inline-block;
  margin-right: 30px;

  &:last-child {
    margin-right: 0;
  }
}

.socials__link {
  width: 150px;
  height: 45px;
  background-color: $dark;
  display: flex;
  border-radius: 3px;
}

.socials__icon {
  width: 40px;
  height: 45px;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border-right: 1px solid $darkGray;
}

.socials__pic {
  width: 16px;
  height: 16px;
}

.socials__svg {
  width: 100%;
  height: 100%;
  fill: #fff;
}

.socials__text {
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-family: 'Roboto', sans-serif;
  color: #fff;
  font-size: 12px;
  font-weight: bold;
  padding: 19px 27px;
}

