.footer {
  height: 212px;
  width: 100%;
  background-color: $dark;
}

.footer__container {
  display: flex;
}

.footer__part {
  width: 25%;
  height: 100%;
  margin-top: 40px;
}

.footer__title {
  font-family: 'Roboto', sans-serif;
  font-size: 11px;
  font-weight: bold;
  color: #9B9A9B;
  text-transform: uppercase;
  display: inline-block;
  height: 45px;
}

.footer__link {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #fff;
  font-weight: bolder;
}

.footer__app {
  display: inline-block;

  &:first-child {
    margin-right: 25px;
  }
}

.footer__app-icon {
  width: 20px;
  height: 20px;
  display: inline-block;
  margin-right: 10px;
}

.footer__svg {
  width: 100%;
  height: 100%;
  fill: #fff;
}

.footer__copyright {
  font-family: 'Roboto', sans-serif;
  font-size: 12px;
  color: #737373;


}