.section {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 150%;
}

.section__top {
  width: 100%;
  height: 263px;
  background-color: #000;
  position: relative;
  
  overflow: hidden;
}

.section__middle {
  width: 100%;
  background-color: #fff;
  display: flex;
  height: 100%;
}

.section__left {
  flex-grow: 1;
  height: 100%;

}

.section__right {
  width: 330px;
  height: 100%;
  flex-grow: 0;
  flex-shrink: 0;
}