.gallery__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
}

.gallery__item {
  width: 200px;
  height: 110px;
  position: relative;

  &:last-child::after {
    content: 'показать все';
    text-transform: uppercase;
    font-family: 'Roboto', sans-serif;
    color: #fff;
    font-size: 12px;
    font-weight: bold;

    display: inline-block;
    padding: 16px 30px;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 3px;

    position: absolute;
    top: 35px;
    left: 25px;

    cursor: pointer;
  }
}

.gallery__img {
  width: 100%;
  height: 100%;
}