.best__title {
  font-family: 'Roboto', sans-serif;
  font-size: 22px;
  font-weight: bold;
  color: $dark;
  display: flex;
  align-items: center;
  height: 115px;
}

.best__link {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  color: #5F5F5F;

  &:after {
    content: '';
    display: inline-block;
    width: 10px;
    height: 10px;
    border-right: 1px solid $dark;
    border-bottom: 1px solid $dark;
    transform: rotate(-45deg);
    margin-left: 5px;
  }
}