.event {
  display: flex;
  align-items: center;
  height: 150px;
  border-top: 1px solid $gray;
}

.event__date {
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  font-size: 44px;
  color: #202020;
}

.event__month-time {
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 14px;
  color: #202020;
  width: 115px;
}

.event__place {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  color: #202020;
  margin-right: auto;
}

.button--event {
  width: 150px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $red;
}