.hashtags__list {
  display: flex;
  margin-bottom: 25px;
}

.hashtags__item {
  margin-right: 10px;

    &:last-child {
      margin-right: 0px;
    }
}

.hashtags__link {
    text-transform: uppercase;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    font-size: 9px;
    padding: 7px 10px;
    color: #fff;
    background-color: rgba(255, 255, 255, 0.3);
    border-radius: 3px;
    
}

