

.person__bg {
  background-color: #000;
  filter: blur(25px);
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 263px;
  position: absolute;
  // z-index: -10;
  // display: flex;
}

.person {
  // position: relative;
  width: 100%;
  height: 263px;
  display: flex;

}

.person__left {

  width: 360px;
  flex-grow: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.person__right {

  flex-grow: 1;
  color: #fff;
  z-index: 10;
  display: flex;
  flex-direction: column;

  align-items: flex-start;
}

.person__foto {
  width: 300px;
  height: 160px;
  z-index: 10;
}

.person__name {
  font-family: 'Roboto', sans-serif;
  font-size: 24px;
  font-weight: bold;
  margin-top: 30px;
}

.person__bithplace, .person__events {
  font-size: 14px;
}

.person__events {
  margin-bottom: 30px;
}


.button--timetable {
  padding: 18px 126px;
}