.search {
  display: flex;
  align-items: center;
}

.search__icon {
  width: 16px;
  height: 16px;
  margin-right: 5px;
}

.search__svg {
  width: 100%;
  height: 100%;
  fill: #fff;
}

.search__field {
  background-color: $darkGray;
  color: #fff;
  border: none;
  font-size: 16px;

  &::-webkit-input-placeholder {
    color: #fff;
    font-weight: bold;
    text-transform: uppercase;
  }
  &::-moz-placeholder {
    color: #fff;
    font-weight: bold;
    text-transform: uppercase;
  }
  &:-moz-placeholder {
    color: #fff;
    font-weight: bold;
    text-transform: uppercase;
  }
  &:-ms-input-placeholder {
    color: #fff;
    font-weight: bold;
    text-transform: uppercase;
  }
}